header{
    min-height: 70px;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border-bottom:  1px solid silver;
    
}
header .logo{
    flex: 1;
}
header a{
    text-transform: uppercase;
    color: rgb(0, 0, 0);
    font: .5em sans-serif;
}

header a:hover{
    
    color: rgb(0, 0, 0);
    
}





header ul li{
    display: inline-block;
    opacity: 0.7;
    padding: 0 20px;
    font-size: 1.8em;
}
header ul li:hover{
    opacity: 1;
   
}
.menu{
    display: none;
}
.cart-icon{
    position: relative;
    margin-right: 20px;
}
.cart-icon span{
    background: rgb(255, 155, 175);
    border-radius: 20px;
    color: white;
    position: absolute;
    top:-10px;
    right: -10px;
    padding: 5px 7px;
    font-size: 10px;
}