.load-page{
   
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
  align-items: center;
    overflow: hidden;
}
.loader{
    width: 200px;
    height: 200px;
    margin: auto;
}
.loader div{
    width: calc(100% - 15px);
    height: calc(100% - 15px);
    border: 2px solid #fff;
    border-top: 2px solid #35b88f;
    border-radius: 50%;
    animation: rotate 10s linear infinite alternate-reverse;
}
@keyframes rotate{
    50%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }

    50%{

        transform: rotate(360deg);
    }

    100%{

        transform: rotate(0deg);
    }

}