.login-page{
    max-width: 500px;
    border: none;
    border-radius: 5px;
    padding: 30px;
    margin: 50px auto;
}
.login-page h2{
    text-transform: uppercase;
    letter-spacing: 2px;
    color: rgb(0, 0, 0);
    font-size: 1em ;
}
.login-page form input,
.login-page form button{
    width: 100%;
    height: 40px;
    margin: 10px 0;
    padding: 0 5px;
    border-bottom: 1px solid  #35b88f;
}
.login-page form .row{
    display: flex;
    justify-content: space-between;
    align-items: center;
}




.login-page form button{
    width: 100px;
    background: #35b88f;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
}






.login-page form a{
    
    background: black;
    color: white;
    letter-spacing: 1.3px;
    text-transform: uppercase;
    padding: 12px 7px 12px 7px;
   
}



.login-page form button:hover{

    opacity: 0.7;

}

.login-page form a:hover{
   
    opacity: 0.7;

}
