.categories{
    max-width: 700px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin: 50px auto;
}
.categories form{
    width: 100%;
    margin-bottom: 10px;
}
.categories label{
    display: block;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 10px;
}
.categories input, button{
    height: 35px;
    border: none;
    outline: none;
    border-bottom: 1px solid #555;
}
.categories input{
    width: 100px;
}
.categories button{
    width: 80px;
    margin-top: 1rem;
    background:  #35b88f;
    color: white;
    margin-left: 7px;
    border-radius: 5%;
}


.categories button:hover{

    background-color: black;
}



.categories .row{
    min-width: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    margin-bottom: 5px;
    
}

