.cart{
    position: relative;
   
    transform: scaleY(0.98);
   
}


.cart-icon{

    margin-top: 1em;

}


.amount span{
    color:  #35b88f;
    padding: 0 20px;
}
.amount button{
    width: 40px;
    height: 40px;
    border: 1px solid #777;
}
.delete{
    position: absolute;
    top:0;
    
    right: 5px;
    color: crimson;
    font-weight: 900;
    cursor: pointer;
}
.total{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.total h3{
    color: rgb(0, 0, 0);
    font-size: 1rem;
}

h2{

    color: black !important;
    font-size: 1rem;
}



.btnn{

    color: rgb(255, 255, 255);
    background-color: black;
    font-size: 1rem;
    padding: 8px 8px 8px 8px;
    border-radius: 5%;
}