.create_product{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}
.upload{
    max-width: 300px;
    height: 300px;
    width: 90%;
 
    padding: 1px;
    margin: 2px;
    position: relative;
}
#file_up{
    position: relative;
    width: 100%;
    height: 100%;
    outline: none;
}
#file_up::before{
    content: "+";
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    background: rgb(0, 0, 0);
    color: rgb(255, 242, 242);
    font-size: 17rem;
    text-align: center;
    cursor: pointer;
    margin: auto;
}
#file_img{
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left: 0;
    background: white;
}
#file_img img{
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
}
#file_img span{
    position: absolute;
    top: -13px;
    right: -13px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 50%;
    padding: 6px 10px;
    cursor: pointer;
    font-weight: 900;
    color: crimson;
}

.create_product form{
    max-width: 300px;
    min-width: 200px;
    width: 100%;
    margin: 10px 20px;
}
.create_product form .row{
    width: 100%;
    margin: 10px 0;
}
.create_product form input, textarea{
    width: 100%;
    min-height: 50px;
    
   
}
.create_product form button{
    width: 200px;
    height: 40px;
    background:  #35b88f;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
}

label{

    margin:5px;
    padding: 5px;
}