.product_card{
    max-width: 300px;
    overflow: hidden;
 
    padding: 5px;
    box-shadow: 0 0 15px #ddd;
    margin: 10px 0;
   
    position: relative;
}
.product_card img{
    width: 100%;
    height: 200px;
    display: block;
    object-fit: cover;
}
.product_box h2{
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-transform: capitalize;
    cursor: pointer;
    color: #000000;
}
.product_card span{
    color: rgb(240, 147, 166);
}
.product_box p{
    width: 100%;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    height: 70px;
    overflow: hidden;
    color: #323232;
}
.row_btn{
    width: 100%;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}
.row_btn a{
    width: 50%;
    text-align: center;
    text-transform: uppercase;
    color: white;
    font-weight: 600;
    letter-spacing: 2px;
    padding: 6px;
}
#btn_buy{
    background: rgb(0, 0, 0);
    color: white;
    
}
#btn_view{
    background: #35b88f;
    margin-left: 5px;
}

#btn_buy:hover{
  opacity: 0.7;
    
}
#btn_view:hover{
    opacity: 0.7;
}



.product_card input{
    position: absolute;
    width: 25px;
    height: 25px;
    top: 4;
   
}